body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.beer-glass {
  width: 9em;
  height: 12.6em;
  border: 3px solid #000;
  border-top: 1px solid transparent;
  border-radius: 15px;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  margin: auto; }
  .beer-glass .beer-glass-liquid {
    width: 100%;
    height: 73%;
    display: inline-grid;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    border-top: none;
    border: 1px solid #000;
    border-radius: 11px;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px; }
  .beer-glass .beer-glass-foam {
    width: 100%;
    height: 27%;
    border: 1px solid #000;
    background-color: #F7E7D8;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px; }
  .beer-glass .beer-description {
    font-size: 1em;
    line-height: 1.5em;
    text-align: left; }

.layout .header {
  position: fixed;
  z-index: 1;
  width: 100%; }

.layout .content {
  margin-top: 64px; }

.page-layout {
  padding: 24px 0;
  background: #fff; }
  .page-layout .page-content {
    padding: 0 24px;
    min-height: 280px; }
  .page-layout .page-sider {
    background: #fff; }
  .page-layout .fixed-page-sider {
    overflow: auto;
    height: 90vh;
    position: fixed;
    left: 0;
    top: 60px;
    overflow-x: hidden; }
  .page-layout .fixed-page-content {
    padding: 0 24px;
    min-height: 280px;
    margin-left: 250px; }

.text-center {
  text-align: center; }

